import { gql, useQuery } from '@apollo/client';
import React from 'react';

const UseHello = () => {
  return useQuery(gql`
    query hello {
      hello {
        sucess
        error
        data
      }
    }
  `);
};

function TestApp() {
  const { data, loading, error } = UseHello();
  if (data) {
    console.log(data);
  }
  return <div>Hello world</div>;
}

export default TestApp;
